.skills-div{
  position: absolute;

  height: 60vh;
  width: 30%;

  top: 12vh;
  left: 60%;



  
}

.h2-skills {
  font-size: 10vh;
  margin-bottom: 30px;
  font-weight: 700;
}

.skills {
  position:relative;
  padding: 8vh 0 0 0;
}
.skills ul {
  padding-top: 25px;
  list-style:none;
  text-align: left;

}
.skills ul > li {
  margin-bottom: 35px;
  background: #dedede;
  height: 10px;
  width: 100%;
}
.skills ul > li em {
  font-size: 2.5vh;
  position: relative;
  top: -30px;
}
.skills ul > li span {
  height: inherit;
  background-color: #000;
  background: linear-gradient(90deg, #000, red);
  position: absolute;
  left: 0;
}
.skills .html5 {
  width: 100%;
  -webkit-animation: html5 2s ease-out;
          animation: html5 2s ease-out;
}
.skills .css3 {
  width: 90%;
  -webkit-animation: css3 2s ease-out;
          animation: css3 2s ease-out;
}
.skills .js {
  width: 100%;
  -webkit-animation: js 2s ease-out;
          animation: js 2s ease-out;
}

.skills .reactjs {
  width: 90%;
  -webkit-animation: reactjs 2s ease-out;
          animation: reactjs 2s ease-out;
}
.skills .nodejs {
  width: 80%;
  -webkit-animation: nodejs 2s ease-out;
          animation: nodejs 2s ease-out;
}

.skills .java {
  width: 90%;
  -webkit-animation: java 2s ease-out;
          animation: java 2s ease-out;
}

.skills .sql {
  width: 80%;
  -webkit-animation: sql 2s ease-out;
          animation: sql 2s ease-out;
}



