.work-button {
    padding: 10px 50px 10px 50px;
    width: 33%;
    background-color: lightgray;
    border: none;
    text-transform: uppercase;
    letter-spacing: 0.5em;
    transition: all 0.2s cubic-bezier(.4,0,.2,1);
}

.work-button:hover {
    letter-spacing: 0.8em;
    background-color: var(--red);
}


.work-button:focus {
    letter-spacing: 0.8em;
    background-color: var(--red);
}

